<template>
  <div class='entry'>
    <div class='entry-info' v-for='(team, key) in teams' :key='key'>
      <div class='entry-team'>
        <div class='entry-member' v-for='(pkmn, key) in team.teamMembers' :key='key'>
          <img :src='`/artwork/${pkmn.name}.png`' />
        </div>
      </div>

      <div class="entry-detail">
        <p class='entry-format' :class='team.tier'>{{team.tier}}</p>

        <button
          class='square_latest_entry_visit'
          @click='$router.push({path:`/battlepark/${team.tier}/${team._id}`})'
        >view</button>
      </div>
    </div>
  </div>
</template>


<script>
import axios from "axios";
export default {
  data() {
    return {
      teams: {}
    };
  },
  async created() {
    try {
      const res = await axios.post("/mysquad/shared/latest");
      this.teams = res.data.teams;
    } catch (err) {
      alert(err);
    }
  }
};
</script>

